/* ============== HEADER ============== */
/* ==================================== */
.header {
  position: relative;
  z-index: 50;
}

.header_fixed {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: 26rem;
}

.header_not_fixed {
  position: absolute;
  width: 100%;
}

.header-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 2rem 0 2rem 0;
  line-height: 1;
}

@media (max-width: 991px) {
  .header-container {
    padding-top: 1rem;
    padding-bottom: 1rem;
    align-items: stretch;
  }
}

.header-container__left {
  position: relative;
}

.header-container__right {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-container__wrapper {
  flex-basis: 50%;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 991px) {
  .header-container__wrapper {
    flex-basis: auto;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
  }
}

/* logo */
.logo {
  overflow: hidden;
  position: relative;
  margin: 0.7rem 0 0;
  width: 23.8rem;
  flex-shrink: 0;
  z-index: 5;
}

.logo::before {
  content: '';
  display: block;
  padding-top: 25.21%;
}

.logo a,
.logo > span {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 5;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.logo a > span,
.logo > span > span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.logo a > span:active, .logo a > span:focus,
.logo > span > span:active,
.logo > span > span:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.logo .logo_dark img,
.logo .logo_light img {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  pointer-events: none;
  background-repeat: no-repeat;
  background-size: contain;
  will-change: opacity;
  transition: opacity 0.8s;
}

.logo .logo_light {
  opacity: 0;
}

.logo-theme-light .logo .logo_dark {
  opacity: 0;
}

.logo-theme-light .logo .logo_light {
  opacity: 1;
}

@media (max-width: 991px) {
  .logo {
    width: 8rem;
    height: 5.083rem;
  }
}

.header-container__right .jsNav__item {
  background: transparent;
  border-radius: 6px;
}

.logo-theme-dark .header-container__right .jsNav__item {
  background: #fff;
  border-radius: 6px;
  transition: background 0.8s;
}

/* logo end */
/* lang */
.lang {
  font-family: "circe-extra-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  color: #3f3f3f;
  font-size: 1.4rem;
  line-height: 2.6rem;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 991px) {
  .lang {
    order: -1;
  }
}

.lang__inner {
  height: 2.6rem;
  width: 2.6rem;
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  color: inherit;
  transition: color 0.35s;
  z-index: 1;
}

.lang__inner::before {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 4rem;
  width: 4rem;
  border-radius: 4rem;
  margin: -2.1rem 0 0 -2rem;
  opacity: 0;
  background-color: black;
  transition: opacity 0.35s, transform 0.35s, background-color 0.35s;
  transform: translateZ(0) scale3d(0.5, 0.5, 1);
  z-index: -1;
}

.lang__inner:hover::before {
  opacity: 0.35;
  transform: translateZ(0) scale3d(1, 1, 1);
}

.logo-theme-light .lang__inner {
  color: white;
}

.logo-theme-light .lang__inner::before {
  background-color: white;
}

/* lang end */
.play_button {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 10;
  opacity: 1;
  background: url(../img/play_video_on_slider.svg) no-repeat center center;
  outline: none;
  border: none;
}

.play_button.o {
