/* ============ NAVIGATION ============ */
/* ==================================== */
.nav-wrap {
  position: relative;
  flex: 1 0 auto;
  text-align: center;
  z-index: 50;
}

@media (max-width: 991px) {
  .nav-wrap {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 48rem;
    max-width: 100%;
    text-align: left;
    background-color: #fff;
    z-index: 999;
    transition-property: opacity, visibility, transform;
    transition-duration: 0.2s;
    opacity: 0;
    transform: translate(-100%, 0);
    transform: translate3d(-100%, 0, 0);
  }
}

@media (max-width: 767px) {
  .nav-wrap {
    width: 32rem;
  }
}

@media (max-width: 639px) {
  .nav-wrap {
    width: 100%;
  }
}

@media (max-width: 991px) {
  .nav-wrap.is-open {
    opacity: 1;
    transform: translate(0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.nav-overlay {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 998;
  transition: opacity 0.2s;
  opacity: 0;
  pointer-events: none;
  display: none;
}

@media (max-width: 991px) {
  .nav-overlay {
    display: block;
  }
}

.nav-wrap.is-open ~ .nav-overlay {
  opacity: 1;
  pointer-events: auto;
}

.nav-opener {
  position: relative;
  height: 4rem;
  width: 2.5rem;
  font-size: 0;
  line-height: 0;
  cursor: pointer;
  display: none;
}

.nav-opener::before, .nav-opener::after {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: 50%;
}

.nav-opener::before {
  height: 3.7rem;
  width: 4.6rem;
  background-image: url("data:image/svg+xml;base64,");
  color: #000;
}

@media (max-width: 991px) {
  .nav-opener {
    display: block;
  }
}

.nav-opener.is-open::before {
  display: none;
}

.nav-opener.is-open::after {
  display: block;
}

.nav-header {
  position: relative;
  align-items: center;
  justify-content: space-between;
  border-bottom: 0.1rem solid rgba(255, 255, 255, 0.5);
  padding: 1rem;
  display: none;
}

@media (max-width: 991px) {
  .nav-header {
