  background-position: 50%;
  background-size: 85%;
}

.nav-close-btn {
  position: relative;
  display: block;
  height: 4rem;
  width: 3rem;
  margin-left: auto;
  text-indent: -999rem;
}

.nav-close-btn::before {
  content: '';
  display: block;
  height: 2.2rem;
  width: 2.2rem;
  position: absolute;
  left: 35%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: 50%;
  background-image: url("data:image/svg+xml;base64,");
